
import cl from './Button.module.css'


export const Button = ({children = 'Hi', type, size = 'medium', variant=''})=>{
    const mainCN = [cl.button + 
        cl[size],
        cl[variant]]

    return(
        <button
        className={mainCN}
        type={type}
        >
            {children}
        </button>
    )
}